
.loading-screen{
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgb(54, 54, 54);
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    -webkit-flex-direction:column;
}
  
.loading-bar-outer{
    width:200px;
    height:30px;
    border:2px solid rgb(255, 255, 255);
    background-color: rgb(0, 0, 0);
    border-radius: 10px;
    margin-top:1em
}
  
.loading-bar-inner{
    background-color: rgb(9, 230, 230);
    height: 100%;
    border-radius: 10px;
}