*{
  margin:0;
  padding:0;
  box-sizing: border-box;
  color:red
}

.app.browserView{
  width: 100%;
  /* border: 2px solid red; */
  background-color: rgb(27, 27, 27);
}

.app-window.browser{
  width: 100%;
  display: flex;
  flex-direction: row;
}

.header.browser{
  display: flex;
  flex-basis: 100%;
  display: -webkit-flex;
  -webkit-flex-basis: 100%;
}

.left-container.browser{
  display: flex;
  flex-basis: 70%;
  flex-direction: column;
  border:2px solid white
  /* background-color: rgba(128, 98, 0, 0.383); */
}

.right-container.browser{
  display: flex;
  flex-basis: 30%;
  flex-direction: column;
  /* background-color: rgba(128, 0, 128, 0.383); */
}

