.map.browserView{
    display: flex;
    flex-direction: column;
    display: -webkit-flex;
    -webkit-flex-direction:column;
    width:100%;
    height:50vh;
    /* padding: 1em; */
    border:2px dotted purple
  }
  .map-wrapper.browser{
    /* height: 100%; */
    width: 100%;
    /* border:2px dotted green */
  }
  .map-svg.browser{
    width:100%;
    height:100%;
    background-color: rgba(6, 139, 117, 0.5);
  }
  .boundary{
  stroke-width:1px;
  stroke:black;
  fill:red
  }
  /* for map click selection */
  .boundary.deselected{
    stroke:blue;
    fill:blue
  }
  